import Experience from '../Experience.js'
import gsap from 'gsap'
import {Howl, Howler} from 'howler';

export default class Learning {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.world = this.experience.world
        this.time = this.experience.time
        this.ui = this.experience.debug.tab.pages[0]
        this.uiVect = this.experience.debug.tab.pages[1]
        this.camera = this.experience.camera.instance
        this.dragControl = this.experience.camera.dragControls
        this.orbitControl = this.experience.camera.controls
        this.playHeadBar = document.getElementById('playHeadBar')
        this.playHead = document.getElementById('playHead')
        this.closeButton = document.getElementById('closeButton')

        // UI
        this.chapter1 = this.ui.addButton({
            title: 'What is motor?',
            label: 'Chapter 1',
            disabled: true
        })
        this.chapter2 = this.ui.addButton({
            title: 'How it works?',
            label: 'Chapter 2',
            disabled: true
        })
        this.chapter3 = this.ui.addButton({
            title: 'Let\'s do the math.',
            label: 'Chapter 3',
            disabled: true
        })

        // ------------ temp -----------------
        // this.camHelper = this.ui.addButton({
        //     title: 'camHelper',
        //     label: 'help'
        // })
        // this.camHelper.on('click', () => {
        //     console.log(this.camera.position)
        //     console.log(this.orbitControl.target)
        // })
        //////////////////////////////////////

        // Misc
        this.chap1IsPlaying = false
        this.chap2IsPlaying = false
        this.chap3IsPlaying = false

        this.setBar()
        this.setUI()
        this.setAudio()
        this.setTimeline()
    }

    disableInteraction() {
        this.dragControl.enabled = false
        this.orbitControl.enabled = false
        this.experience.debug.tab.pages[1].disabled = true
        this.experience.debug.tab.pages[0].disabled = true
    }

    enableInteraction() {
        this.dragControl.enabled = true
        this.orbitControl.enabled = true
        this.experience.debug.tab.pages[1].disabled = false
        this.experience.debug.tab.pages[0].disabled = false
    }

    closeLearning() {
        this.playHeadBar.style.display = 'none'
        this.enableInteraction()
        Howler.stop()
        if (this.chap1IsPlaying) {
            this.chap1tl.pause().invalidate()
            this.world.learningMisc.direction.scale.set(0, 0, 0)
            this.chap1IsPlaying = false
        } else if (this.chap2IsPlaying) {
            this.chap2tl.pause().invalidate()
            this.chap2IsPlaying = false
            this.world.rightHands.hand1.position.x = 0
            this.world.rightHands.hand2.position.x = 0
            this.world.fieldVect.vect1.scale.set(1, 1, 1)
            this.world.fieldVect.vect2.scale.set(1, 1, 1)
            this.world.forceVect.vect1.scale.set(1, 1, 1)
            this.world.forceVect.vect2.scale.set(1, 1, 1)
            this.world.rightHands.addHandRotation = 0
            this.world.learningMisc.direction.scale.set(0, 0, 0)
        } else if (this.chap3IsPlaying) {
            this.chap3IsPlaying = false
        }
    }

    setBar() {
        this.closeButton.addEventListener('click', () => {
            this.closeLearning()
        })
        this.playHead.addEventListener('pointerdown', () => {
            this.playheadIsClicked = true
            this.playHead.addEventListener('pointerup', () => {
                this.playheadIsClicked = false

                if (this.chap1IsPlaying) {
                    this.chap1Voice.seek(this.playHead.value*this.chap1Voice.duration()/100)
                    this.chap1tl.invalidate().seek(this.playHead.value*this.chap1Voice.duration()/100)
                } else if (this.chap2IsPlaying) {
                    this.chap2Voice.seek(this.playHead.value*this.chap2Voice.duration()/100)
                    this.chap2tl.invalidate().seek(this.playHead.value*this.chap2Voice.duration()/100)
                } else if (this.chap3IsPlaying) {
                    this.chap3Voice.seek(this.playHead.value*this.chap3Voice.duration()/100)
                }
            }, {once: true})
        })
    }

    setUI() {
        this.chapter1.on('click', () => {
            this.playHeadBar.style.display = 'block'
            this.disableInteraction()

            this.chap1Voice.play()
            this.world.rightHands.isShowed = false
            this.world.fieldVect.isShowed = false
            this.world.forceVect.isShowed = false
            this.uiVect.refresh()
            this.chap1tl.play(0)
            this.chap1IsPlaying = true
        })
        this.chapter2.on('click', () => {
            this.playHeadBar.style.display = 'block'
            this.disableInteraction()

            this.chap2Voice.play()
            this.world.rightHands.isShowed = true
            this.world.fieldVect.isShowed = true
            this.world.forceVect.isShowed = true
            this.uiVect.refresh()
            this.chap2tl.play(0)
            this.chap2IsPlaying = true
        })
        this.chapter3.on('click', () => {
            this.playHeadBar.style.display = 'block'
            this.disableInteraction()

            this.chap3Voice.play()
            this.chap3IsPlaying = true
        })
    }

    setAudio() {
        this.chap1Voice = new Howl({
            src: ['audio/chap1Voice.mp3'],
            onload: () => {
                this.chapter1.disabled = false
            },
            onend: () => {
                this.closeLearning()
            }
        })

        this.chap2Voice = new Howl({
            src: ['audio/chap2Voice.mp3'],
            onload: () => {
                this.chapter2.disabled = false
            },
            onend: () => {
                this.closeLearning()
            }
        })

        this.chap3Voice = new Howl({
            src: ['audio/chap3Voice.mp3'],
            onload: () => {
                this.chapter3.disabled = false
            },
            onend: () => {
                this.closeLearning()
            }
        })
    }

    setTimeline() {
        // chapter 1 ------------------------------------------------------------------------------------------
        this.chap1tl = gsap.timeline()
        // initialize
        this.chap1tl.to(this.camera.position, {x: 1.56, y: 1.88, z: -0.01, duration: 2.5}, 0)
        this.chap1tl.to(this.orbitControl.target, {x: 0, y: 0.64, z: 0, duration: 2.5}, 0)
        this.chap1tl.set(this.world.rotor.model.rotation, {x: 0}, 0)
        this.chap1tl.set(this.world.rotor, {omega: 0}, 0)
        this.chap1tl.to(this.world.switch.model.position, {duration: 0.2, delay: 0, x: 0.323}, 0)
        this.chap1tl.to(this.world.switch.model.rotation, {duration: 0.2, delay: 0, z: 16*Math.PI/180}, 0)
        this.chap1tl.to(this.world.slider.model.position, {x: 0.355}, 0)
        this.chap1tl.set(this.world.learningMisc.direction.scale, {x: 0, y: 0, z: 0}, 0)
        // to battery
        this.chap1tl.to(this.camera.position, {x: 0.71, y: 1.05, z: 0.33, duration: 2}, 6)
        this.chap1tl.to(this.orbitControl.target, {x: 0.34, y: 0.66, z: -0.34, duration: 2}, 6)
        // to rotor
        this.chap1tl.to(this.camera.position, {x: 0.88, y: 1.20, z: -0.28, duration: 2.8}, 9.4)
        this.chap1tl.to(this.orbitControl.target, {x: 0.0, y: 0.68, z: 0.01, duration: 2.8}, 9.4)
        // to switch
        this.chap1tl.to(this.camera.position, {x: 0.84, y: 1.27, z: -0.77, duration: 2}, 14.4)
        this.chap1tl.to(this.orbitControl.target, {x: 0.32, y: 0.70, z: -0.35, duration: 2}, 14.4)
        // switch on
        this.chap1tl.to(this.world.switch.model.position, {duration: 0.2, delay: 0, x: 0.321}, 16.5)
        this.chap1tl.to(this.world.switch.model.rotation, {duration: 0.2, delay: 0, z: -16*Math.PI/180}, 16.5)
        // to rotor
        this.chap1tl.to(this.camera.position, {x: 0.88, y: 1.20, z: -0.28, duration: 2.8}, 17.4)
        this.chap1tl.to(this.orbitControl.target, {x: 0.0, y: 0.68, z: 0.01, duration: 2.8}, 17.4)
        // to close up
        this.chap1tl.to(this.camera.position, {x: 0.68, y: 1.29, z: 0.20, duration: 4.8}, 42.4)
        this.chap1tl.to(this.orbitControl.target, {x: 0.0, y: 0.64, z: -0.01, duration: 4.8}, 42.4)
        // to see direction of rotation
        this.chap1tl.to(this.camera.position, {x: -0.96, y: 0.68, z: 0.00, duration: 4.2}, 52.4)
        this.chap1tl.to(this.orbitControl.target, {x: 0.0, y: 0.68, z: 0.00, duration: 4.2}, 52.4)
        this.chap1tl.to(this.world.learningMisc.direction.scale, {x: 1, y: 1, z: 1, duration: 1})
        // grab and swing
        this.chap1tl.to(this.world.rotor.model.rotation, {x: 330*Math.PI/180, duration: 1.4}, 59.8)
        this.chap1tl.to(this.world.rotor.model.rotation, {x: 270*Math.PI/180, duration: 0.2, ease: 'power1.in'})
        this.chap1tl.set(this.world.rotor, {omega: -3*Math.PI})
        // to see current
        this.chap1tl.to(this.world.learningMisc.direction.scale, {x: 0, y: 0, z: 0, duration: 1}, 78)
        this.chap1tl.to(this.camera.position, {x: 0.68, y: 1.29, z: 0.20, duration: 6}, 78)
        this.chap1tl.to(this.orbitControl.target, {x: 0.0, y: 0.64, z: -0.01, duration: 6}, 78)
        // to end
        this.chap1tl.to(this.camera.position, {x: 1.49, y: 1.87, z: -1.10, duration: 6}, 89)
        this.chap1tl.to(this.orbitControl.target, {x: 0, y: 0.64, z: 0, duration: 6}, 89)
        // set to pause
        this.chap1tl.pause()

        // chapter 2 ------------------------------------------------------------------------------------------
        this.chap2tl = gsap.timeline()
        // initialize
        this.chap2tl.to(this.camera.position, {x: 1.10, y: 1.13, z: -0.33, duration: 3.5}, 0)
        this.chap2tl.to(this.orbitControl.target, {x: 0, y: 0.64, z: 0, duration: 3.5}, 0)
        this.chap2tl.to(this.world.switch.model.position, {duration: 0.2, delay: 0, x: 0.321}, 0)
        this.chap2tl.to(this.world.switch.model.rotation, {duration: 0.2, delay: 0, z: -16*Math.PI/180}, 0)
        this.chap2tl.set(this.world.rightHands.hand1.position, {x: 999}, 0)
        this.chap2tl.set(this.world.rightHands.hand2.position, {x: 999}, 0)
        this.chap2tl.set(this.world.fieldVect.vect1.scale, {x: 0, y: 0, z: 0}, 0)
        this.chap2tl.set(this.world.fieldVect.vect2.scale, {x: 0, y: 0, z: 0}, 0)
        this.chap2tl.set(this.world.forceVect.vect1.scale, {x: 0, y: 0, z: 0}, 0)
        this.chap2tl.set(this.world.forceVect.vect2.scale, {x: 0, y: 0, z: 0}, 0)
        this.chap2tl.set(this.world.rightHands, {addHandRotation: 0}, 0)
        this.chap2tl.to(this.world.slider.model.position, {x: 0.355}, 0)
        this.chap2tl.set(this.world.learningMisc.direction.scale, {x: 0, y: 0, z: 0}, 0)
        // grab
        this.chap2tl.to(this.world.rotor.model.rotation, {x: -30*Math.PI/180, duration: 1.8}, 8)
        this.chap2tl.to(this.world.rotor.model.rotation, {x: -30*Math.PI/180, duration: 66.8})
        this.chap2tl.set(this.world.rotor, {omega: 0}, 8)
        this.chap2tl.to(this.world.rotor, {omega: 0, duration: 68.6}, 8)
        // to left side
        this.chap2tl.to(this.camera.position, {x: 0.25, y: 0.86, z: -0.11, duration: 2}, 11)
        this.chap2tl.to(this.orbitControl.target, {x: 0.01, y: 0.71, z: 0.08, duration: 2}, 11)
        // show hand1
        this.chap2tl.set(this.world.rightHands.hand1.position, {x: 0}, 26)
        this.chap2tl.to(this.camera.position, {x: -0.22, y: 0.88, z: -0.11, duration: 2}, 25.6)
        this.chap2tl.to(this.orbitControl.target, {x: -0.01, y: 0.70, z: 0.10, duration: 2}, 25.6)
        // show field vect1
        this.chap2tl.to(this.world.fieldVect.vect1.scale, {x: 1, y: 1, z: 1, duration: 1}, 32.2)
        this.chap2tl.to(this.camera.position, {x: -0.36, y: 0.74, z: 0.073, duration: 2}, 32.2)
        this.chap2tl.to(this.orbitControl.target, {x: -0.01, y: 0.72, z: 0.073, duration: 2}, 32.2)
        // rotate hand
        this.chap2tl.set(this.world.rightHands, {addHandRotation: 0}, 35)
        this.chap2tl.to(this.world.rightHands, {addHandRotation: 90*Math.PI/180}, 35)
        this.chap2tl.to(this.world.rightHands, {addHandRotation: 180*Math.PI/180}, 36)
        this.chap2tl.to(this.world.rightHands, {addHandRotation: 270*Math.PI/180}, 37)
        this.chap2tl.to(this.world.rightHands, {addHandRotation: 360*Math.PI/180}, 38.7)
        // show force vect1
        this.chap2tl.to(this.world.forceVect.vect1.scale, {x: 1, y: 1, z: 1, duration: 1}, 45)
        this.chap2tl.to(this.camera.position, {x: -0.36, y: 0.72, z: 0.08, duration: 2}, 43)
        this.chap2tl.to(this.orbitControl.target, {x: -0.01, y: 0.70, z: 0.08, duration: 2}, 43)
        // to right side
        this.chap2tl.to(this.camera.position, {x: -0.31, y: 0.90, z: 0.07, duration: 3}, 49.8)
        this.chap2tl.to(this.orbitControl.target, {x: 0.01, y: 0.60, z: -0.08, duration: 3}, 49.8)
        // show hand2
        this.chap2tl.set(this.world.rightHands.hand2.position, {x: 0}, 58.4)
        this.chap2tl.to(this.camera.position, {x: 0.31, y: 0.90, z: 0.07, duration: 2}, 58)
        this.chap2tl.to(this.orbitControl.target, {x: 0.01, y: 0.62, z: -0.09, duration: 2}, 58)
        // show field vect2
        this.chap2tl.to(this.world.fieldVect.vect2.scale, {x: 1, y: 1, z: 1, duration: 1}, 62)
        this.chap2tl.to(this.camera.position, {x: 0.45, y: 0.66, z: -0.10, duration: 2}, 62)
        this.chap2tl.to(this.orbitControl.target, {x: 0.01, y: 0.62, z: -0.10, duration: 2}, 62)
        // show force vect2
        this.chap2tl.to(this.world.forceVect.vect2.scale, {x: 1, y: 1, z: 1, duration: 1}, 66.4)
        this.chap2tl.to(this.camera.position, {x: 0.36, y: 0.80, z: -0.12, duration: 2}, 65.4)
        this.chap2tl.to(this.orbitControl.target, {x: 0.01, y: 0.64, z: -0.09, duration: 2}, 65.4)
        // to see direction of rotation
        this.chap2tl.to(this.camera.position, {x: -0.96, y: 0.68, z: 0.00, duration: 4.2}, 70)
        this.chap2tl.to(this.orbitControl.target, {x: 0.0, y: 0.68, z: 0.00, duration: 4.2}, 70)
        this.chap2tl.to(this.world.learningMisc.direction.scale, {x: 1, y: 1, z: 1, duration: 1}, 86.65)
        this.chap2tl.to(this.world.learningMisc.direction.scale, {x: 0, y: 0, z: 0, duration: 1}, 94.8)
        // grab to 90d
        this.chap2tl.to(this.world.rotor.model.rotation, {x: -90*Math.PI/180, duration: 2}, 104)
        this.chap2tl.to(this.world.rotor.model.rotation, {x: -90*Math.PI/180, duration: 1}, 106)
        this.chap2tl.set(this.world.rotor, {omega: 0}, 104)
        this.chap2tl.to(this.world.rotor, {omega: 0, duration: 1}, 104)
        // grab to 0d
        this.chap2tl.to(this.world.rotor.model.rotation, {x: 0, duration: 1.5}, 111.5)
        this.chap2tl.to(this.world.rotor.model.rotation, {x: 0, duration: 1}, 113)
        this.chap2tl.set(this.world.rotor, {omega: 0}, 113)
        this.chap2tl.to(this.world.rotor, {omega: 0, duration: 1}, 113)
        // to end
        this.chap2tl.to(this.camera.position, {x: 1.49, y: 1.87, z: -1.10, duration: 4}, 132)
        this.chap2tl.to(this.orbitControl.target, {x: 0, y: 0.64, z: 0, duration: 4}, 132)
        // set to pause
        this.chap2tl.pause()
    }

    update() {
        this.updatePlayhead()
    }

    updatePlayhead() {
        if (this.playheadIsClicked) {
            return
        } else if (this.chap1IsPlaying) {
            this.playHead.value = (this.chap1Voice.seek()/this.chap1Voice.duration())*100
        } else if (this.chap2IsPlaying) {
            this.playHead.value = (this.chap2Voice.seek()/this.chap2Voice.duration())*100
        } else if (this.chap3IsPlaying) {
            this.playHead.value = (this.chap3Voice.seek()/this.chap3Voice.duration())*100
        }
    }
}